import { withAuth0, useAuth0 } from "@auth0/auth0-react";
import React,{useEffect} from "react";
import {useNavigate} from 'react-router-dom'
import {CircularProgress} from '@mui/material'
import {withRouter} from './withRouter'
import {Backdrop} from '@mui/material'



class Profile extends React.Component
{
  //Constructor for Profile below. Use this if you would like to customize the properties of the component.
  /*constructor(props)
  {
    super(props)
  }*/

  componentDidMount()
  {
    const { user, isAuthenticated, isLoading,getAccessTokenSilently } = this.props.auth0;
    const navigate = this.props.navigate
    
    if (isLoading) {
      return <CircularProgress style={{'color':'#193A6F'}}/>
    }
    localStorage.setItem("user",JSON.stringify(user))
    if(!isAuthenticated)
    {
      setTimeout(() => navigate('/'))
    }
    return (
      isAuthenticated && (
          <img style={{borderRadius:40}} width="30%" height="30%" src={user.picture} alt={user.name} />
      )
    );
  }

  render()
  {
    const {isLoading, isAuthenticated,user,getAccessTokenSilently} = this.props.auth0
    const userDetails = user || "Loading"
    window.dispatchEvent(new Event('storage'))
    if(isLoading)
    {
      return <CircularProgress style={{'color':'#193A6F'}}/>
    }
    if(user===null||user===undefined)
    {
      setTimeout(()=>this.props.navigate('/'))
    }
    getAccessTokenSilently().then(token => localStorage.setItem("token",token))
    localStorage.setItem('user',JSON.stringify(user))
    const url = "https://priam-connections.netlify.app/.netlify/functions/upvotes"
    try
    {
      const grabUser = JSON.parse(localStorage.getItem("user")) || user
      fetch(url,{headers:{'Content-Type': 'text/plain'},method:'POST',body:JSON.stringify({email:grabUser.email})}).then(res=>res.json()).then(data=>localStorage.setItem("upvotedPosts",JSON.stringify(data[0].upvotedPosts)))    
    }
    catch(err)
    {
      setTimeout(()=>this.props.navigate('/'))
    }

    localStorage.setItem("user",JSON.stringify(user))
    if(!isAuthenticated)
    {
      setTimeout(() => this.props.navigate('/'))
    }
    return (
      isAuthenticated && (
          <img style={{borderRadius:40}} width="30%" height="30%" src={user.picture} alt={user.name} />
      )
    );
  }
}


