import Checkbox from '@mui/material/Checkbox';
import React from 'react';
import Favorite from '@mui/icons-material/Favorite';
import FavoriteBorder from '@mui/icons-material/FavoriteBorder';


class CustomCheckbox extends React.Component{
    constructor(props){
        const condition = false
        super(props)
        this.state = {
            checked: condition,
            id: undefined,
            user: JSON.parse(localStorage.getItem("user")),
        }
        this.toggleCheckbox = this.toggleCheckbox.bind(this)
        this.handleUpVote = this.handleUpVote.bind(this)
    }

    onClick = (event) =>{
        event.stopPropagation()
        
        const postID = this.props.id
        const user = this.state.user
        fetch('https://priam-connections.netlify.app/.netlify/functions/checkvote',
        {
            method: 'POST',
            headers: {
            'Content-Type': 'text/plain',
        },
        body: JSON.stringify({email:user.email,identification:postID})})
        this.toggleCheckbox()
    }


    handleUpVote()
    {
        const postID = this.state.id
        const user = this.state.user
        fetch('https://priam-connections.netlify.app/.netlify/functions/checkvote',
        {
            method: 'POST',
            headers: {
            'Content-Type': 'text/plain',
        },
        body: JSON.stringify({email:user.email,identification:postID})
        
  })
  const url = "https://priam-connections.netlify.app/.netlify/functions/upvotes"
    const grabUser = JSON.parse(localStorage.getItem("user"))
    fetch(url,{headers:{'Content-Type': 'text/plain'},method:'POST',body:JSON.stringify({email:grabUser.email})}).then(res=>res.json()).then(data=>localStorage.setItem("upvotedPosts",JSON.stringify(data[0].upvotedPosts)))
}

    toggleCheckbox() {
        this.setState((prevState) => ({
            checked: !prevState.checked,
            id: this.props.id
        }))
    }

    componentDidMount()
    {
        
        const url = "https://priam-connections.netlify.app/.netlify/functions/upvotes"
        const user = JSON.parse(localStorage.getItem("user"))
        fetch(url,{headers:{'Content-Type': 'text/plain'},method:'POST',body:JSON.stringify({email:user.email})}).then(res=>res.json()).then(data=>localStorage.setItem("upvotedPosts",JSON.stringify(data[0].upvotedPosts)))
        const upvotedPosts= JSON.parse(localStorage.getItem("upvotedPosts"))
        const condition = upvotedPosts.includes(this.props.id)
        this.setState({
            checked: condition,
            id: this.props.id
        }, () => {
        });
    }

    render(){
        
        return (
            <div onChange={this.toggleCheckbox} onClick = {this.onClick}>
            <Checkbox
            checked={this.state.checked}
            icon={<FavoriteBorder/>}
            checkedIcon={<Favorite/>}
            onChange={(event) => event.stopPropagation()}/>
            </div>
        )
    }
}

export default CustomCheckbox;