//Create unique theme for Card, Grid, Tooltip, Chip colors

class NewPost extends React.Component
{
  constructor(props)
  {
    super(props)
    this.state = {
      open:false,
      selected:0,
      tagCategories:[
        {
            "category":"Departments",
            "color":"#0d3394",
            "subCategories":
            [
                "College of Agriculture",
                "Reginald F. Lewis College of Business",
                "College of Education",
                "College of Engineering and Technology",
                "College of Humanities and Social Sciences",
                "College of Natural and Health Sciences"
            ]
        },
        {
            "category":"Buildings",
            "color":"#94260d",
            "subCategories":["Hunter McDaniel Building","Engineering and Technology Building","Lula Johnson Hall"]
        },
        {
            "category":"Residency",
            "color":"#945e0d",
            "subCategories":
            [
                "Freshman Housing",
                "Upperclass Housing",
                "Branch Hall",
                "Otelia Howard Hall",
                "Lucretia Campbell Hall",
                "Langston Hall",
                "Seward Hall",
                "Quad I",
                "Williams Hall",
                "Gateway Hall",
                "Moore Hall",
                "Quad II",
                "Whiting Hall"
            ]
        }
    ]
    }
  }
  getIndividualTags()
  {
    var individualTags = []
    this.state.tagCategories.forEach((tag) =>
    {
      individualTags = individualTags.concat(tag.subCategories)
    })
    return individualTags
  }
  render()
  {
    const enqueueSnackbar = this.props.enqueueSnackbar
    const snackbarMessage = (message,variant) =>
    {
      enqueueSnackbar(message, { variant: variant })
    }
    const staticTheme = DarkTheme()
    const theme = createTheme(DarkTheme())
    return (
      <Auth0Provider
      domain="kingtechnician.us.auth0.com"
      clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW"
      >
      <ThemeProvider theme={theme}>
        <ResponsiveAppBar/>
      <div className="App">
        <div className="App-header">
          <br></br>
          <CustomizedSteppers/>
          <SpinnerMenu/>
        </div>
      </div>
      </ThemeProvider>
      </Auth0Provider>
    )
  }
}

function NewPos()
{
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [selected, setSelected] = useState(0)
  if(isMobile)
  {
    return (
      <Auth0Provider
      domain="kingtechnician.us.auth0.com"
      clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW"
      >
        <ResponsiveAppBar/>
      <div className="App">
        <div className="App-header">
          <h1>This will be rendered only in mobile view</h1>
          <SpinnerMenu/>
        </div>
      </div>
      </Auth0Provider>
    )
  }
  return (
    <Auth0Provider
    domain="kingtechnician.us.auth0.com"
    clientId="xTbGmROcQrGne2nH7AAEDu1C3E8hKeBW"
    >
      <ResponsiveAppBar/>
    <div className="App">
      <div className="App-header">
        <h1>This will be rendered only in desktop view</h1>
        <SpinnerMenu/>
      </div>
    </div>
    </Auth0Provider>
  )
}

export default withSnackbar(NewPost);